@keyframes navtogglein{
   from{
      opacity: 0;
      transform:translate(0,-2rem);
   }
   to{
      opacity: 1;
      transform:translate(0, 0);
   }
}
@keyframes jump{
  0%{
      opacity: 0;
      transform:translate(0, 0);
   }
  50%{
      opacity: 1;
      transform:translate(0, -0.6rem);
   }
   100%{
      opacity: 1;
      transform:translate(0, 0);
   }
}
@keyframes navitemin{
   from{
      opacity: 0;
      transform: translate(0,-3rem);
   }
   to{
      opacity: 1;
      transform: translate(0, 0);
   }
}
@keyframes shake{
    0%{
        opacity: 0;
        transform:translate(-2rem, 0);
     }
    25%{
        opacity: 1;
        transform:translate(2rem, 0);
     }
     50%{
        opacity: 0;
        transform:translate(-2rem, 0);
     }
    75%{
        opacity: 1;
        transform:translate(2rem, 0);
     }
     100%{
        opacity: 1;
        transform:translate(0, 0);
     }
  }