form{
   animation: shake 0.5s 1 ease-in-out forwards;
}
fieldset, fieldset legend {
   border: 2px solid #CCC;
   border-radius: 0.3rem;
   box-shadow: 0 0 2rem 0 rgba(black, 0.1);
}
fieldset {
   background: #FFF;
   padding: 3.5vh 4.5vw 3vh 4.5vw;
}
fieldset legend {
   background: #FFF;
   display: inline-block;
   padding: 0.6em 2.2em;
   font-weight: bold;
}
label {
   font-weight: bold;
   display: block;
}
input[type='text'], input[type='password'] {
   width: 20rem;
   display: block;
   border: 2px solid #CCC;
   padding: 0.4em 1em;
   border-radius: 0.4em;
   background: #EEE;
   transition: background 0.3s ease, border-color 0.3s ease;
   outline: none;
}
input[type='text']:focus, input[type='password']:focus {
   background: #FFF;
   border-color: #333; 
} 
input[type='submit']{
   border: 2px solid #CCC;
   background: #333;
   color: #FFF;
   padding: 0.6em 2.2em;
   border-radius: 0.4em;
   cursor: pointer;
   font-weight: bold;
   transition: background 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}
input[type='submit']:hover {
   background: #CCC;
   color: #333;
   border-color: #333; 
}